<template>
  <el-row class="tac sidebar">
    <el-col :span="24">
      <el-menu
        :default-active="$route.path"
        class="el-menu-vertical-demo menu"
        :unique-opened="true"
        router
        background-color="#545c64"
        text-color="#FFF"
        active-text-color="#ffd04b">
        <div class="head_box">
          <div style="width: 57%; float: left">
            <img src="/static/img/head_img/default.png" class="head_img" alt="">
          </div>
          <div class="user_box">
            <span class="user_name">Guest</span>
            <DropOne class="user_select" :DropOneStyle="DropOneStyle" :DropOneItems="DropOneItems"></DropOne>
          </div>
        </div>
        <br/>
        <el-menu-item :key="this.home.name" :index="this.home.route">
          <i :class="this.home.icon"></i>
          <span slot="title">{{this.home.name}}</span>
        </el-menu-item>
        <SideMenu :menuItems="menuItems"></SideMenu>
      </el-menu>
    </el-col>
  </el-row>
</template>

<script>
  import DropOne from '../Tool/DropOne'
  import SideMenu from './SideMenu'
  export default {
    name: 'Sidebar',
    data: function () {
      return {
        DropOneStyle: 'drop_sidebar',
        DropOneItems: {
          0: {name: 'Profile', isDisabled: true, isDivided: false, href: 'Profile'},
          1: {name: 'Contacts', isDisabled: false, isDivided: false, href: 'Contacts'},
          2: {name: 'Mailbox', isDisabled: false, isDivided: false, href: 'Mailbox'},
          3: {name: 'Login Out', isDisabled: false, isDivided: true, href: 'Login Out'}
        },
        home: {route: '/', name: 'Home', icon: 'el-icon-more'},
        menuItems: [
          {
            first: {
              route: '/System/index',
              name: 'System',
              icon: 'el-icon-setting'
            },
            second: [
              {
                first: {
                  route: '/Auth/index',
                  name: 'Auth',
                  icon: 'fab fa-app-store'
                },
                second: null
              },
              {
                first: {
                  route: '/Auth/Client',
                  name: 'Client',
                  icon: 'fab fa-app-store'
                },
                second: null
              },
              {
                first: {
                  route: '/User/index',
                  name: 'User',
                  icon: 'fa fa-user'
                },
                second: null
              }
            ]
          }
        ]
      }
    },
    components: {
      DropOne,
      SideMenu
    },
    computed: {
      options () {
        return this.$store.state.options
      },
      activeIndex: {
        get () {
          return this.$store.state.activeIndex
        },
        set (val) {
          this.$store.commit('set_active_index', val)
        }
      }
    },
    mounted () {
      // 刷新时以当前路由做为tab加入tabs
      if (this.$route.path !== '/' && this.$route.path.indexOf('userInfo') === -1) {
        this.$store.commit('add_tabs', {route: '/', name: 'Home'})
        this.$store.commit('add_tabs', {route: this.$route.path, name: this.$route.name})
        this.$store.commit('set_active_index', this.$route.path)
      } else {
        this.$store.commit('add_tabs', {route: '/', name: 'Home'})
        this.$store.commit('set_active_index', '/')
        this.$router.push('/')
      }
    },
    watch: {
      '$route' (to) {
        let flag = false
        for (let option of this.options) {
          if (option.name === to.name) {
            flag = true
            this.$store.commit('set_active_index', '/' + to.path.split('/')[1])
            break
          }
        }
        if (!flag) {
          this.$store.commit('add_tabs', {route: '/' + to.path.split('/')[1], name: to.name})
          this.$store.commit('set_active_index', '/' + to.path.split('/')[1])
        }
      }
    }
  }
</script>

<style>
  .sidebar {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 13%;
  }
  .head_box {
    margin-left: 16%;
    margin-top: 10%;
  }

  .head_box img {
    width: 75px;
    height: 75px;
    border-radius: 50%;
  }

  .user_box {
    padding-top: 10%;
  }

  .user_box .user_name {
    color: antiquewhite;
    font-weight: 200;
    margin-left: -15px;
  }

  .user_box .user_select {
    margin-left: -15px;
  }

  .menu {
    min-height: 953px;
  }

</style>




