<template>
  <el-dialog title="Add AuthRule" :visible.sync="dialog_status" :before-close="closeDialog" @open="callParent" width="30%">
    <el-form :model="form" ref="form">
      <el-form-item prop="rule_id" label="Parent" :label-width="label_width">
        <el-select v-model="form.rule_id" filterable placeholder="请选择" class="input">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="title" label="Title" :label-width="label_width">
        <el-input v-model="form.title" class="input"></el-input>
      </el-form-item>
      <el-form-item prop="region" label="活动区域" :label-width="label_width">
        <el-select v-model="form.region" placeholder="请选择活动区域" class="input">
          <el-option label="区域一" value="shanghai"></el-option>
          <el-option label="区域二" value="beijing"></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="closeDialog">取 消</el-button>
      <el-button type="primary" @click="addRule">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    data () {
      return {
        dialog_status: false,
        form: {
          title: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: '',
          rule_id: ''
        },
        label_width: '120px',
        topRule: '',
        options: [
          {label: 'Top Menu', value: 0}
        ],
        loading: {
          status: false,
          text: 'Loading....',
          background: 'rgba(0, 0, 0, 0.3)'
        }
      }
    },
    methods: {
      openAdd () {
        this.topRule = this.common.apiPost(this, [], '/AuthRule/getTopRule', 'dialog_status')
      },
      closeDialog () {
        this.$refs['form'].resetFields()
        this.dialog_status = false
      },
      addRule () {
        this.common.apiPost(this, '', '/AuthRule/index')
      },
      callParent () {
        this.$emit('listenAdd', false)
      }
    }
  }
</script>

<style>
  .input {
    width: 80%;
  }
</style>



