<template>
  <div>
    <el-submenu v-for="menuItem in menuItems" v-if="menuItem.second&&menuItem.first"
                :key="menuItem.first.route" :index="menuItem.first.name">
      <template slot="title">
        <i :class="menuItem.first.icon"></i>
        <span> {{menuItem.first.name}}</span>
      </template>
      <SideMenu :menuItems="menuItem.second"></SideMenu>
    </el-submenu>

    <el-menu-item v-for="menuItem in menuItems" v-if="menuItem.second==null&&menuItem.first"
                  :key="menuItem.first.route" :index="menuItem.first.route">
      <i :class="menuItem.first.icon"></i>
      <span slot="title">{{menuItem.first.name}}</span>
    </el-menu-item>
  </div>
</template>

<script>
  export default {
    name: 'SideMenu',
    data () {
      return {}
    },
    props: ['menuItems']
  }
</script>



