import Vue from 'vue'
import Router from 'vue-router'
import index from '@/Index/index'
import login from '@/Login/index'
import register from '@/Login/register'
import System from '@/System/index'
import User from '@/User/index'
import Auth from '@/Auth/index'

Vue.use(Router)

export default new Router({
  routes: [
    // 登录页面
    {
      path: '/login',
      name: 'login',
      component: login
    },
    // 注册页面
    {
      path: '/register',
      name: 'register',
      component: register
    },
    // 默认首页
    {
      path: '/',
      name: 'Home',
      component: index
    },
    {
      path: '/System/index',
      name: 'System',
      component: System
    },
    // 权限页面
    {
      path: '/Auth/index',
      name: 'Auth',
      component: Auth
    },
    // 用户页面
    {
      path: '/User/index',
      name: 'User',
      component: User
    },
    {
      path: '*',
      redirect: '/'
    }
  ]
})



