<template>
  <div>
    <div>
      <i class="el-icon-search"></i>
      <el-input
        placeholder="请输入搜索内容"
        v-model="search"
        clearable style="width: 200px">
      </el-input>
      <!--<el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">Home</el-breadcrumb-item>
        <el-breadcrumb-item>活动管理</el-breadcrumb-item>
        <el-breadcrumb-item>活动列表</el-breadcrumb-item>
        <el-breadcrumb-item>活动详情</el-breadcrumb-item>
      </el-breadcrumb>-->
      <el-button type="text" class="sign" @click="login"><i class="fas fa-sign-in-alt"></i>Sign In</el-button>
      <!--<el-button type="text">Login Out</el-button>-->
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        search: ''
      }
    },
    methods: {
      login () {
        this.$router.push('/login')
      }
    }
  }
</script>

<style>
  .sign {
    color: #909399;
    font-size: 16px;
    float: right;
  }

  .sign i {
    font-size: 17px;
    margin-right: 10px;
  }
</style>



