<template>
  <el-dropdown @command="handleCommand" trigger="click">
  <span :class="DropOneStyle">
    下拉菜单<i class="el-icon-arrow-down el-icon--right"></i>
  </span>
    <el-dropdown-menu slot="dropdown">
      <el-dropdown-item v-for="item in DropOneItems" :key="item.index" :command="item.href" :disabled="item.isDisabled" :divided="item.isDivided" >{{ item.name }}</el-dropdown-item>
    </el-dropdown-menu>
  </el-dropdown>
</template>

<style>
  .el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
  }

  .drop_sidebar {
    cursor: pointer;
    color: #8095a8;
  }

  .el-icon-arrow-down {
    font-size: 12px;
  }
</style>

<script>
  export default {
    data: function () {
      return {
      }
    },

    props: ['DropOneStyle', 'DropOneItems'],

    methods: {
      handleCommand (command) {
        this.$message('click on item ' + command)
      }
    }
  }
</script>



