<template>
  <div>
    <div class="tool_bar">
      <el-button type="success" size="small" icon="fa fa-plus" @click="openAddRule" :loading="loading.button">&nbsp;&nbsp;Add</el-button>
      <el-button type="primary" size="small" icon="fa fa-edit">&nbsp;&nbsp;Edit</el-button>
      <el-button type="warning" size="small" icon="fa fa-stop">&nbsp;&nbsp;stop</el-button>
      <el-button type="danger" size="small" icon="fa fa-trash-alt">&nbsp;&nbsp;del</el-button>
    </div>
    <addRule ref="addRule" v-on:listenAdd="closeButtonLoad"></addRule>
    <div class="tree_box">
      <el-tree
        :data="data"
        :props="defaultProps"
        accordion
        @node-click="handleNodeClick">
      </el-tree>
    </div>
  </div>
</template>

<script>
  import addRule from './add'
  export default {
    data () {
      return {
        addDialog: false,
        data: [{
          label: '一级 1',
          children: [{
            label: '二级 1-1',
            children: [{
              label: '三级 1-1-1'
            }]
          }]
        }, {
          label: '一级 2',
          children: [{
            label: '二级 2-1',
            children: [{
              label: '三级 2-1-1'
            }]
          }, {
            label: '二级 2-2',
            children: [{
              label: '三级 2-2-1'
            }]
          }]
        }, {
          label: '一级 3',
          children: [{
            label: '二级 3-1',
            children: [{
              label: '三级 3-1-1'
            }]
          }, {
            label: '二级 3-2',
            children: [{
              label: '三级 3-2-1'
            }]
          }]
        }, {
          label: '一级 3',
          children: [{
            label: '二级 3-1',
            children: [{
              label: '三级 3-1-1'
            }]
          }, {
            label: '二级 3-2',
            children: [{
              label: '三级 3-2-1'
            }]
          }]
        }, {
          label: '一级 3',
          children: [{
            label: '二级 3-1',
            children: [{
              label: '三级 3-1-1'
            }]
          }, {
            label: '二级 3-2',
            children: [{
              label: '三级 3-2-1'
            }]
          }]
        }
        ],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        loading: {
          status: false,
          button: false,
          text: 'Loading....',
          background: 'rgba(0, 0, 0, 0.3)'
        }
      }
    },
    methods: {
      handleNodeClick (data) {
        console.log(data)
      },
      openAddRule () {
        this.loading.button = true
        this.$refs.addRule.openAdd()
      },
      closeButtonLoad (bool) {
        this.loading.button = bool
      }
    },
    components: {
      addRule: addRule
    }
  }
</script>

<style>
  .tool_bar {
    margin-top: -20px;
    margin-bottom: 10px;
  }

  .tree_box {
    height: 30%;
    width: 15%;
  }
</style>



