<template>
  <div id="app">
    <el-container v-if="$route.path === '/login' || $route.path === '/register'">
      <router-view/>
    </el-container>
    <el-container v-else="$route.path !== '/login' || $route.path !== '/register'">
      <el-aside width="12.7%">
        <Sidebar></Sidebar>
      </el-aside>
      <el-container>
        <el-header>
          <Header></Header>
        </el-header>
        <el-container>
          <HeaderTab></HeaderTab>
          <el-main>
            <router-view/>
          </el-main>
          <el-footer>
            <Footer></Footer>
          </el-footer>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>

<script>
  import Sidebar from './Common/SideBar'
  import Header from './Common/Header'
  import Footer from './Common/Footer'
  import HeaderTab from './Common/HeaderTab'

  export default {
    name: 'app',
    components: {
      Header,
      Footer,
      HeaderTab,
      Sidebar
    },
    methods: {},
    computed: {
      options () {
        return this.$store.state.options
      },
      activeIndex: {
        get () {
          return this.$store.state.activeIndex
        },
        set (val) {
          this.$store.commit('set_active_index', val)
        }
      }
    }
  }
</script>




